import { Inject, Injectable, Optional } from '@angular/core';
import { CanActivate } from '@angular/router';
import {
  CardsManagementJourneyConfiguration,
  CardsManagementJourneyConfigurationToken,
  getDefaultConfigValues,
} from '../cards-management-journey-config.service';

@Injectable({
  providedIn: 'root',
})
export class CardsManagementTravelNoticeGuard implements CanActivate {
  private _config: CardsManagementJourneyConfiguration;

  canActivate(): boolean {
    return this._config.enableTravelNotice;
  }

  constructor(
    @Optional()
    @Inject(CardsManagementJourneyConfigurationToken)
    private readonly config: CardsManagementJourneyConfiguration
  ) {
    this._config = getDefaultConfigValues(this.config);
  }
}
